

html {
    height: 100vh;
    width: 100%;
    background-image: url(./assets/4826101.jpg);
    background-size: cover;
    /* background-color: rgb(40, 35, 35); */
}

.header-app {
    height: 50px;
    width: 100%;
    display: flex;
    margin-bottom: 5px;
    justify-content: center;
    align-content: center;
    background-color: rgb(148 38 38 / 33%);
}

.logo {
    height: auto;
    width: 100px;
    background-image: url('./assets/Marvel_Logo.svg.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

.cards {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.card-item {
    width: 450px;
    height: 200px;
    margin: 10px;
    display: flex;
    justify-items: stretch;
    text-align: center;
    color: aliceblue;
    border-radius: 10px;
    font-family: 'Bangers', cursive;
    letter-spacing: 3px;
    background-color: rgba(110, 31, 31, 0.612);
}

.search {
    text-align: center;
    margin-bottom: 20px;
}

