.btn {
    width: 60px;
    height: 33px;
    font-size: medium;
    font-family: 'Bangers', cursive;
}


input {
    width: 25%; 
    height: 30px;
    border: none;
    border-radius: 10px 0 0 10px;
    font-size: medium;
    font-family: 'Bangers', cursive;
    background-color: rgba(240, 248, 255, 0.721);
}

input::placeholder {
    color: black;
    font-size: medium;
    font-family: 'Bangers', cursive;
}

h2 {
    margin-top: 35px;
}

img {
    width: 50%;
    border-radius: 10px;
}


@media only screen and (max-width: 770px)  {
    .card-item {
        display: none;
    }

    .cards {
        text-align: center;
        width: 100%;
    }

    .name-hero{
        color: aliceblue;
    }
    
    input {
        width: 70%;
    }

}

@media only screen and (min-width: 770px)  {
    
    .none {
        display: none; 
    }
    
    .name-hero {
        display: none; 
    }
    
}